
import 'common/css/reset.css'
import 'common/js/config.js'
import {isWeiXin,weixinShare} from 'common/js/common.js'
import geturlpara from 'common/js/geturlpara.js'
import AjaxServer from 'common/js/ajaxServer.js'
import Failed from "components/Failed/Failed"
import Coupons from "components/Coupons/Coupons"
import Vue from 'vue'
import VueResource from 'vue-resource'
import Loading from 'vux/src/components/loading/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import querystring from 'vux/src/tools/querystring/index.js'
import cookie from 'vux/src/tools/cookie/index.js'

Vue.prototype.$geturlpara=geturlpara
Vue.use(VueResource)
export default {
name: 'coupon',
data () {
return {
id:"",
loadingshow: true,
loadtext: 'loading...',
showContent:false,
failedshow:false,
failedmsg:"服务请求失败，请刷新重试",
couponData:{}
}
},
components: {
Group,
Cell,
Loading,
Failed,
Coupons
},
beforeCreate(){

var id = this.$geturlpara.getUrlKey("id");
console.log(id);
},
created () {
var id = this.$geturlpara.getUrlKey("id");
this.id=id;
if (id) {
this.fetchData(id);
}else{
this.loadingshow=false;
this.failedmsg="请求参数有误"
this.failedshow=true;
}
},
mounted(){
},
methods: {

fetchData(id){
var self=this;
AjaxServer.httpGet(
Vue,
HOST+'/api/coupons/'+id+'.json',
{},
(data)=>{
self.loadingshow=false;
if (data.status==0) {
self.showContent=true;
this.couponData=data;
}else{
self.failedshow=true;
self.failedmsg=data.error;
console.log(data.error);
}
},
(err)=>{
console.log(err);
self.loadingshow=false;
self.failedshow=true;
}
);
setTimeout(()=>{
self.loadingshow=false;
},10000);
},
}
}



